$kob-font: 'Source Sans Pro', Calibri, sans-serif;
body{
	font-family: $kob-font;
	font-size: 14px;
}
h1,
h2,
h3,
h4,
h5,
h6{
	margin: 0;
	padding: 0;
	font-family: $kob-font;
	font-weight: 600;
	color: $kob-gray;
}
p{
	margin: 0;
	padding: 0;
	font-size: 14px;
}


