/* DayPicker styles */

.DayPicker {
  display: -ms-flexbox;
  display: flex;
  display: -webkit-flex;
  -ms-flex-wrap: wrap;
      flex-wrap: wrap;
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
  position: relative;
  padding: 1rem 0;
  -webkit-user-select: none;
     -moz-user-select: none;
      -ms-user-select: none;
          user-select: none;
}

.DayPicker-Month {
  display: table;
  border-collapse: collapse;
  border-spacing: 0;
  -webkit-user-select: none;
     -moz-user-select: none;
      -ms-user-select: none;
          user-select: none;
  margin: 0 1rem;
}

  .DayPicker-NavBar {
    position: absolute;
    left: 0;
    right: 0;
    padding: 0 .5rem;
  }

  .DayPicker-NavButton {
    position: absolute;
    width: 1.5rem;
    height: 1.5rem;
    background-repeat: no-repeat;
    background-position: center;
    background-size: contain;
    cursor: pointer;
  }

    .DayPicker-NavButton--prev {
      left: 1rem;
      background-image: url("data:image/svg+xml;base64,");
    }

    .DayPicker-NavButton--next {
      right: 1rem;
      background-image: url("data:image/svg+xml;base64,");
    }


  .DayPicker-Caption {
    display: table-caption;
    height: 1.5rem;
    text-align: center;
  }

  .DayPicker-Weekdays {
    display: table-header-group;
  }

    .DayPicker-WeekdaysRow {
      display: table-row;
    }

      .DayPicker-Weekday {
        display: table-cell;
        padding: .5rem;
        font-size: .875em;
        text-align: center;
        color: #8b9898;
      }

  .DayPicker-Body {
    display: table-row-group;
  }

    .DayPicker-Week {
      display: table-row;
    }

      .DayPicker-Day {
        display: table-cell;
        padding: .5rem;
        border: 1px solid #eaecec;
        text-align: center;
        cursor: pointer;
        vertical-align: middle;
      }

  .DayPicker--interactionDisabled .DayPicker-Day {
    cursor: default;
  }

/* Default modifiers */

.DayPicker-Day--today {
  color: #d0021b;
  font-weight: 500;
}

.DayPicker-Day--disabled {
  color: #dce0e0;
  cursor: default;
  background-color: #eff1f1;
}

.DayPicker-Day--outside {
  cursor: default;
  color: #dce0e0;
}

/* Example modifiers */

.DayPicker-Day--sunday {
  background-color: #f7f8f8;
}

.DayPicker-Day--sunday:not(.DayPicker-Day--today) {
  color: #dce0e0;
}

.DayPicker-Day--selected:not(.DayPicker-Day--disabled):not(.DayPicker-Day--outside) {
  color: #FFF;
  background-color: #4A90E2;
}

//Custom Styles
.date-range-picker-wrapper{
  position: relative;
  .date-range-picker{
    background: $kob-white;
    box-shadow: rgba(0, 0, 0, 0.117647) 0px 1px 6px, rgba(0, 0, 0, 0.117647) 0px 1px 4px;
    border-radius: 2px;
    z-index: 700;
    top: 45px;
    position: absolute;
    overflow: hidden;
    width: 394px;
    height: 237px;
    opacity: 1;
    transition: height 300ms ease-out,
                opacity 300ms ease-out 200ms;
  }
}
.date-input{
  input{
    border: 0;
    line-height: 26px;
    width: 140px;
    background: transparent;
    border-bottom: 1px solid $kob-gray-eighty;
    @media (max-width: #{$tablet-large + 1px}){
      max-width: 124px;
    }
  }
  span{
    color: $kob-gray-light;
    margin: 0 16px;
  }
}


